import React from "react"
import { Link, graphql } from "gatsby"
import blogStyles from "../styles/blog.module.css"
import NavBar from "../components/NavBar"
import Footer from "../components/Footer"
import { BlogListSection } from "../components/Section"
import Header from "../components/Header"

import BackgroundImage from "gatsby-background-image"

export default function blog({ data }) {
  const blogList = data.Blogs.edges.map(edge => {
    return {
      id: edge.node.id,
      title: edge.node.frontmatter.title,
      date: edge.node.frontmatter.date,
      abstract: edge.node.frontmatter.abstract,
      slug: edge.node.fields.slug,
    }
  })

  return (
    <div>
      <NavBar />
      <BackgroundImage
        Tag="div"
        className={blogStyles.background}
        fluid={data.blogBg.childImageSharp.fluid}
        style={{ opacity: 0.5 }}
      >
        <Header divider="true" style={{ color: "white" }}>
          The Wild Side Of Town <p>{blogList.length} posts</p>{" "}
        </Header>
        <div className={blogStyles.divider}></div>
        <BlogListSection>
          {blogList.map(blog => {
            return (
              <li key={blog.id} className={blogStyles.container}>
                <Link
                  to={blog.slug}
                  style={{ textDecoration: "none", color: "inherit" }}
                >
                  <h2 style={{ color: "#fff" }}>{blog.title}</h2>
                  <p
                    style={{
                      fontSize: "small",
                      color: "#bbb",
                      marginBottom: "1rem",
                    }}
                  >
                    {" "}
                    - {blog.date}
                  </p>
                  <p style={{ color: "#fff" }}>{blog.abstract}</p>
                </Link>
              </li>
            )
          })}
        </BlogListSection>
      </BackgroundImage>
      <Footer />
    </div>
  )
}

export const query = graphql`
  query {
    Blogs: allMarkdownRemark(
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
            abstract
          }
          fields {
            slug
          }
          timeToRead
        }
      }
    }
    blogBg: file(relativePath: { eq: "blogBg.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`
